import React from 'react';
import DisplayAll from './DisplayAll';

class DisplayAllBooks extends React.Component {
    constructor() {
        super();
        this.state = {
            books: [],
        };
    }
    render() {
        const booksAll = this.props.books;
        const book = booksAll.map(item => {
            return <DisplayAll item={item} key={item.id} />
        });
        console.log(book)
        return (
            <div>
                {book}
            </div>
        )
    }
}
export default DisplayAllBooks;